import { useQuery } from "@tanstack/react-query";
import ApexChart from "react-apexcharts";
import { IHistoryData } from "../routes/Charts";
import { fetchCoinHistory } from "../api";

interface IChart {
  coinId: string;
}

function CandleChart({ coinId }: IChart) {
  const { isLoading, data } = useQuery<IHistoryData[]>({
    queryKey: ["ohlcv"],
    queryFn: () => fetchCoinHistory(coinId),
  });

  const candleData =
    data?.map((price) => ({
      x: new Date(price.time_open * 1000), // ms  
      y: [
        Number(price.open),
        Number(price.high),
        Number(price.low),
        Number(price.close),
      ],
    })) ?? [];
  return (
    <ApexChart
      type="candlestick"
      series={[
        {
          name: "",
          data: candleData,
        },
      ]}
      options={{
        theme: {
          mode: "light",
        },
        chart: {
          toolbar: {
            show: false,
          },
          background: "transparent",
        },
        grid: {
          show: false,
        },
        yaxis: {
          show: false,
        },
        xaxis: {
          labels: { show: false },
          axisTicks: {
            show: false,
          },
          axisBorder: { show: false },
        },
      }}
    />
  );
}

export default CandleChart;
