import styled from "styled-components";
import UpArrow from "../components/Icon/UpArrow";
import { useQuery } from "@tanstack/react-query";
import { IPriceData } from "./Coin";
import { fetchCoinTickers } from "../api";
import DownArrow from "../components/Icon/DownArrow";

const Container = styled.div`
  max-width: 480px;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  gap: 10px;
`;

const FullCard = styled.div`
  width: 100%;
  background-color: ${(props) => props.theme.boxColor};
  border-radius: 15px;
  box-shadow: 4px 4px ${(props) => props.theme.borderColor};
  display: flex;
  flex-direction: column;
  gap: 12px;
  padding: 20px;
`;

const GridCard = styled.div`
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 20px;
`;

const HalfCard = styled(FullCard)`
  height: 100px;
  display: flex;
  gap: 12px;
  padding: 20px;
`;

const Title = styled.span`
  font-size: 14px;
  font-weight: 600;
  color: ${(props) => props.theme.accentColor};
`;

const Data = styled.span<IDataProps>`
  font-size: 30px;
  color: ${(props) =>
    props.isPositive === undefined
      ? props.theme.textColor
      : props.isPositive
      ? "red"
      : "green"};
`;

const Icon = styled.div`
  display: flex;
  align-items: center;
  justify-content: space-between;
  svg {
    width: 40px;
    height: 40px;
  }
`;

interface IPros {
  coinId: string;
}

interface IDataProps {
  isPositive?: boolean;
}

function Price({ coinId }: IPros) {
  const { isLoading, data } = useQuery<IPriceData>({
    queryKey: [`tickers, ${coinId}`],
    queryFn: () => fetchCoinTickers(`${coinId}`),
  });

  const change1h = data?.quotes.USD.percent_change_1h;
  const change12h = data?.quotes.USD.percent_change_12h;
  const change24h = data?.quotes.USD.percent_change_24h;
  const change7d = data?.quotes.USD.percent_change_7d;

  return (
    <Container>
      <FullCard>
        <Title>24 </Title>
        <Data>${data?.quotes.USD.volume_24h}</Data>
      </FullCard>
      <GridCard>
        <HalfCard>
          <Title>1 </Title>
          {change1h !== undefined && (
            <Icon>
              <Data isPositive={change1h > 0}>{change1h}%</Data>
              {change1h > 0 ? <UpArrow /> : <DownArrow />}
            </Icon>
          )}
        </HalfCard>
        <HalfCard>
          <Title>12 </Title>
          {change12h !== undefined && (
            <Icon>
              <Data isPositive={change12h > 0}>{change12h}%</Data>
              {change12h > 0 ? <UpArrow /> : <DownArrow />}
            </Icon>
          )}
        </HalfCard>
      </GridCard>
      <GridCard>
        <HalfCard>
          <Title>24 </Title>
          {change24h !== undefined && (
            <Icon>
              <Data isPositive={change24h > 0}>{change24h}%</Data>
              {change24h > 0 ? <UpArrow /> : <DownArrow />}
            </Icon>
          )}
        </HalfCard>
        <HalfCard>
          <Title>7 </Title>
          {change7d !== undefined && (
            <Icon>
              <Data isPositive={change7d > 0}>{change7d}%</Data>
              {change7d > 0 ? <UpArrow /> : <DownArrow />}
            </Icon>
          )}
        </HalfCard>
      </GridCard>
    </Container>
  );
}

export default Price;
